nav {
    display: flex;
    justify-content: center;
    background-color: rgba(15, 15, 15, 0.4);
    position: -webkit-sticky;
    position: sticky;
    z-index: 100;
    top: -1px;
    transition: 350ms;
}

nav:hover {
    background-color: rgba(15, 15, 15, 0.8);
}

.nav-item {
    text-decoration: none;
    margin: 15px;
    font-family: Sans-serif;
    transition: 400ms;
    color: #bbbbbb;
    padding: 5px 10px 5px 10px;
    border-radius: 5px
}

.nav-item:hover {
    color: #ffffff;
}

.header {
    font-size: 100px;
    font-family: Trebuchet MS;
    text-align: center;
    color: rgb(20,20,20);
    margin-bottom: 10px;
}

#quote {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    color: rgb(20,20,20);
    padding-bottom: 20px;
    font-size: 23px;
}
html {
    scroll-behavior: smooth;
}

body {
    margin: 0%;
    background: url("https://www.timeforkids.com/wp-content/uploads/2019/09/final-cover-forest.jpg?w=1024");
    background-attachment: fixed;
    overflow-x: hidden;
    background-position-x: center;
    background-size: 100%;
    font-family: Quicksand;
}
  

p, h1, h2, h3 {
    color: white
}

p {
    font-family: Quicksand, sans-serif, Arial, Helvetica;
    font-size: 20px;
}

.content {
    background-color: #4d4d4d;
    margin-bottom: 0px;
    padding: 15px;
}
.abt-card-cont {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: -15px;
}

.ab-sect-card {
    display: grid;
    align-items: center;
    justify-content: center;
}

.gray {
    background-color: rgb(120, 120, 120);
}

.pic-item  {
    grid-column: 2;
    grid-row: 1 / span 2;
    overflow: hidden;
    width: 400px;
    transition: 0.4s ease;
    border-radius: 5%;
}

.ab-sect-card .ab-sect-cont {
    grid-column: 1;
    grid-row: 2;
}

.ab-sect-card .interest {
    grid-column: 1;
    grid-row: 1;
}

.interest {
    font-size: 66px;
    width: 500px;
    text-align: center;
    padding: 16px 2px;
}

.ab-sect-cont {
    width: 300px;
    padding: 20px;
}

.pic-item img {
    transition: 0.4s ease;
}

.pic-item img:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.pic-item:hover {
    border-radius: 15%;
}

.pic {
    height: 100%;
    width: 100%;
}

.grid-item {
    text-align: center;
    margin: auto;
}

.ab-header {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0px;
    line-height: 200px;
    font-size: 65px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.container {
    overflow: hidden;
}
